import React from 'react'
import '../css/work.css'
import '../css/style.css'
import $ from 'jquery'
import gsap from 'gsap'

var CountLOL = 1;
$(document).ready(function(){
    console.log("workReady!");
   
    $(".LeftIcon").click(function(){
        CountLOL = CountLOL - 1;
        if(CountLOL === 0){
            CountLOL = 3;
        }
        console.log(CountLOL);
        if(CountLOL === 3){
            //earth background
            gsap.to(".background__image", {duration: 1, opacity: 1})
            gsap.to(".mars__background", {duration: 1, opacity: 0})
            gsap.to(".main__text__button button", {duration: 0.5, opacity: 1})
            gsap.to(".top__top__top__earth", {duration: 0.5, opacity: 1})
            gsap.to(".h2__main__text h2", {duration: 0.5, opacity: 1})
            gsap.to(".h1Position h1", {duration: 0.5, opacity: 1})



        }
        if(CountLOL === 2){
            //mars background
            gsap.to(".background__image", {duration: 1, opacity: 0})
            gsap.to(".main__text__button button", {duration: 0.5, opacity: 0})
            gsap.to(".top__top__top__earth", {duration: 0.5, opacity: 0})
            gsap.to(".h2__main__text h2", {duration: 0.5, opacity: 0})           
            gsap.to(".mars__background", {duration: 1, opacity: 1})
            gsap.to(".h1Position h1", {duration: 0.5, opacity: 0})
        }
    })
    $(".NextIcon").click(function(){
        CountLOL = CountLOL - +1;
        console.log(CountLOL);
    })

})
function Work() {
    return (
        <div className="work">
            <div className="nextLeftAndRirrrr">
            <svg className="LeftIcon E" focusable="false" viewBox="0 0 24 24" aria-hidden="true"><path d="M15.41 7.41L14 6l-6 6 6 6 1.41-1.41L10.83 12z"></path></svg>
            <svg className="NextIcon E" focusable="false" viewBox="0 0 24 24" aria-hidden="true"><path d="M10 6L8.59 7.41 13.17 12l-4.58 4.59L10 18l6-6z"></path></svg>
            </div>
            <div className="earth-background">
                <div className="background__poasition">
                    <div className="background__image"></div>
                </div>
            </div>
            <div className="Earth-Text">
            <div className="whole100vh">
                <nav>
                    <div className="ourEarthPosition">
                        <div className="h1Position">
                            <h1>The Earth</h1>
                        </div>
                    </div>
                </nav>
                <main>
                    <div className="main__text__button">
                        <div className="h2__main__text">
                            <h2 className="top__top__top__earth">The earth is the third planet from our sun. </h2>
                            <h2 className="smallerFont">About 29.2% of Earth's surface is land consisting of continents and islands.</h2>
                            <h2 className="smallerFont">The remaining 70.8% is covered with water</h2>
                        </div>
                        <div className="main__text__button">
                            <button>learn more</button>
                        </div>
                    </div>
                </main>
            </div>
        </div>
        <div className="Mars-Background">
            <div className="background__position">
                <div className="mars__background"></div>
            </div>
        </div>
        <div className="mars-text">
            <div className="mars__position">
                <h2 className="header__mars">The Mars</h2>
                <h2 className="top__top__top__mars">Mars is the fourth planet from the Sun and the second-smallest planet in the Solar System.</h2>
                <div className="button__position">
                    <button>learn more</button>
                </div>
            </div>
        </div>
        </div>
        
    )
}

export default Work
