import React from 'react'
import LandingPage from './LandingPage'
import Work from './Work'
import $ from "jquery"
import { gsap } from "gsap";
import '../css/style.css'
import HamburgerMenu from './HamburgerMenu';
import HamburgerButton from './HamburgerButton';
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { ScrollToPlugin } from "gsap/ScrollToPlugin";

gsap.registerPlugin(ScrollTrigger, ScrollToPlugin);
gsap.to(window, {duration: 0.2, scrollTo: 0});
var counter = 0;
$(document).ready(function(){
    $(".closeMenuToWork").click(function(){
        counter = 2;
        console.log("closedToWork");
        gsap.to(".Mars-Background", {display: "block"})
        $(".article1").css("display", "none");
        $(".work").css("display", "block");
        gsap.to(".text", {duration: 0.3, opacity: 0 })
        gsap.to(".text", {delay:0.3, duration: 0, display: "none" })
        gsap.to(".third__layer", {delay: 0.7, duration: 0.7, width: 0})

    })
    $(".closeMenuToHome").click(function(){
        counter = 2;
        console.log("closedToHome");
        $(".article1").css("display", "block");
        $(".work").css("display", "none");
        gsap.to(".text", {duration: 0.3, opacity: 0 })
        gsap.to(".text", {delay:0.3, duration: 0, display: "none" })
        gsap.to(".third__layer", {delay: 0.7, duration: 0.7, width: 0})
    })
})
$(document).ready(function(){
    $(".icon").click(function(){
        //abfrage start overflow
        counter = counter + 1;
        if (counter === 3){
            counter = 1;
        }
        console.log(counter);
        //abfrage ende overflow
        if  (counter === 1){
            if ($(window).height() > 0){
                console.log("größer");
                gsap.to(".Mars-Background", {display: "none"})
                gsap.to(".mars-text", {display: "none"})
                gsap.to(window, {duration: 0.5, scrollTo: 0});
                gsap.to(".third__layer", {delay: 0.5, height: "100%", duration: 0.8, width: "100%"})
                gsap.to(".text", {delay: 1.3,display: "Block" })
                gsap.to(".text", {delay: 1.3, duration: 2, opacity: 1 })
            }else if ($(window).height() === 0){
                console.log("gleich");
                gsap.to(".third__layer", {height: "100%",delay: 0, duration: 0.8, width: "100%"})
                gsap.to(".text", {delay: 0.8,display: "Block" })
                gsap.to(".text", {delay: 0.8, duration: 2, opacity: 1 })
            }
        }
        if (counter === 2){
            counter = 0;
            gsap.to(window, {duration: 0.2, scrollTo: 0});
            gsap.to(".text", {duration: 0.3, opacity: 0 })
            gsap.to(".text", {delay:0.3, duration: 0, display: "none" })
            gsap.to(".text", {delay: 2, display: "none"})
            gsap.to(".third__layer", {delay: 0.7, duration: 0.7, width: 0})
        }
    })
    $(".closeMenu").click(function(){
        counter = 2;
        console.log("closing menu");
        gsap.to(".text", {duration: 0.3, opacity: 0 })
        gsap.to(".text", {delay:0.3, duration: 0, display: "none" })
        gsap.to(".third__layer", {delay: 0.7, duration: 0.7, width: 0})
      });
})
function Home() {
    return (
        <div>
            <HamburgerButton/>
            <HamburgerMenu/>
            <LandingPage/>
            <Work/>
        </div>
    )
}

export default Home
